<template>
  <div class="star text_center">
    <img class="img_middle" :style="{width:width+'px',margin:'0 '+width/7+'px'}" v-for="n in parseInt(score/2)" :src="starFill">
    <img class="img_middle" :style="{width:width+'px',margin:'0 '+width/7+'px'}" v-for="n in (score%2?1:0)" :src="starHalf">
    <img class="img_middle" :style="{width:width+'px',margin:'0 '+width/7+'px'}" v-for="n in Math.floor(5-score/2)" :src="starNone">
  </div>
</template>

<script>
  export default {
    name: 'star',
    props: {
      score: {
        type: Number,
        default: 5
      },
      width: {
        type: Number,
        default: 15
      },
      color: {
        type: String,
        default: 'orange'
      }
    },
    data () {
      return {
        starHalf: require('@/assets/images/home/' + (this.color === 'orange' ? 'star' : 'red') + '-half.png'),
        starFill: require('@/assets/images/home/' + (this.color === 'orange' ? 'star' : 'red') + '-fill.png'),
        starNone: require('@/assets/images/home/star-none.png')
      }
    }
  }
</script>



